import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { Container as ContainerBase } from "components/misc/Layouts.js";
import { ReactComponent as FacebookIcon } from "../../images/facebook-svgrepo-com.svg";
import { ReactComponent as YelpIcon } from "../../images/yelp-svgrepo-com.svg";
import { ReactComponent as GoogleIcon } from "../../images/google-plus-svgrepo-com.svg";
import { ReactComponent as InstagramIcon } from "../../images/instagram-svgrepo-com.svg";
const Container = tw(ContainerBase)`bg-gray-900 text-gray-100 -mx-8 -mb-8`;
const Content = tw.div`max-w-screen-xl mx-auto py-20 lg:py-24`;

const Row = tw.div`flex items-center justify-center flex-col px-8`;

const LogoContainer = tw.div`flex items-center justify-center md:justify-start`;
const LogoText = tw.h5`ml-2 text-2xl font-black tracking-wider`;

const LinksContainer = tw.div`mt-8 font-medium flex flex-wrap justify-center items-center flex-col sm:flex-row`;
const Link = tw.a`border-b-2 border-transparent hocus:text-gray-300 hocus:border-gray-300 pb-1 transition duration-300 mt-2 mx-4`;

const SocialLinksContainer = tw.div`mt-10`;
const SocialLink = styled.a`
  ${tw`cursor-pointer inline-block text-gray-100 hover:text-gray-500 transition duration-300 mx-4`}
  svg {
    ${tw`w-5 h-5`}
  }
`;

const CopyrightText = tw.p`text-center mt-10 font-medium tracking-wide text-sm text-gray-600`;
// eslint-disable-next-line import/no-anonymous-default-export
export default ({
  links = {
    facebook: {
      url: "https://facebook.com/vietopiava",
    },
    instagram: {
      url: "https://www.instagram.com/vietopiava/",
    },
    yelp: {
      url: "https://www.yelp.com/biz/vietopia-harrisonburg",
    },
    google: {
      url: "https://www.google.com/search?q=vietopia",
    },
  },
}) => {
  return (
    <Container>
      <Content>
        <Row>
          <LogoContainer>
            <LogoText>VIETOPIA</LogoText>
          </LogoContainer>
          <LinksContainer>
            <Link href="#">Home</Link>
            <Link href="#menus">Menus</Link>
            <Link href="#contact_us">Contact Us</Link>
          </LinksContainer>
          <SocialLinksContainer>
            {Object.keys(links).map((linkName, index) => (
              <SocialLink key={index} href={links[linkName].url}>
                {linkName === "facebook" && <FacebookIcon />}
                {linkName === "instagram" && <InstagramIcon />}
                {linkName === "yelp" && <YelpIcon />}
                {linkName === "google" && <GoogleIcon />}
              </SocialLink>
            ))}
          </SocialLinksContainer>
          <CopyrightText>
            &copy; Copyright 2021. All Rights Reserved.
          </CopyrightText>
        </Row>
      </Content>
    </Container>
  );
};
